@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@500&display=swap');
.App {
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-image: url("../images/portfolio-mask.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: 'Ubuntu', sans-serif;
}

.App-logo {
    height: 10vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 40s linear;
    }
}

.App-header {
    background-color: #3d4453;
    min-height: 10vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

nav {
    background-color: rgba(55, 55, 71, 0.65);
    ul {
        padding: 0.5rem;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        list-style-type: none;
    }
    a {
        padding: 0.5rem 1rem;
        text-decoration: none;
        font-weight: bold;
        color: rgb(240, 230, 230);
    }
    .active {
        color: rgb(235, 235, 240);
    }
}

// content container
.content-container-outer {
    align-self: flex-end;
    margin: 2rem 6rem 0rem 0rem;
    padding: 0.6rem;
    width: 36rem;
    height: 65vh;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 6px;
}

.content-container {
    height: 26rem;
    width: 34rem;
    position: absolute;
    top: 11rem;
    padding: 0.5rem 1rem;
    padding-bottom: 2rem;
    background: url('../images/film_grain-small.gif');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}

h1,
h2 {
    text-shadow: 0px 1px 1px #4d4d4d;
    color: #222;
    font: 70px 'LeagueGothicRegular';
}

//ABOUT
.about {
    h4 {
        padding-left: 1.5rem;
        //margin-top: 7.5rem;
    }
    .about-text {
        margin-top: -2rem;
        padding-left: 1.5rem;
        padding-bottom: 0.2rem;
    }
    .icons {
        padding: 0rem 1.2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        list-style: none;
        li {
            i {
                font-size: 3rem;
                color: rgb(7, 54, 7);
                &:hover {
                    -webkit-transform: rotate(360deg);
                    -webkit-transition-duration: 0.2s;
                }
            }
        }
    }
}

// PROJECTS
.projects-container {
    margin-top: -3rem;
    display: flex;
    justify-content: space-between;
    z-index: 1;
    .project {
        // width: 30%;
        padding: 0rem 0.5rem;
        text-align: center;
        img {
            width: 100%;
            object-fit: contain;
            border-radius: 7px;
            &:hover {
                box-shadow: 0px 0px 50px 21px black;
            }
        }
    }
}

//CONTACT
.contact {
    display: flex;
    flex-direction: column;
    .contact-icons {
        display: flex;
        justify-content: space-around;
        align-items: center;
        list-style: none;
        a {
            color: rgb(57, 8, 61);
            text-decoration: none;
            font-size: 3.5rem;
            &:hover {
                box-shadow: 0px 0px 50px 27px rgba(0, 0, 0, 0.75);
                background-color: gray;
            }
        }
    }
    .mail-btn {
        color: rgb(57, 8, 61);
        text-align: center;
        border: 3px solid rgb(71, 74, 148);
        border-radius: 0.5rem;
        padding: 0.5rem 1rem;
        text-decoration: none;
        align-self: center;
        margin-top: 2rem;
        &:hover {
            box-shadow: 0px 0px 50px 27px rgba(0, 0, 0, 0.75);
        }
    }
}

// Audio Player
.react-audio-player {
    height: 2.3rem;
    margin-left: -34rem;
    margin-top: 2rem;
    border-radius: 20px;
    box-shadow: 0px 0px 50px 27px rgb(49, 47, 47);
}

audio::-webkit-media-controls-panel {
    background-color: rgba(59, 59, 90, 0.5)
}

